
.yd-list-theme4 .yd-list-item .yd-list-img{
	width:0;
	height:0;
	overflow:hidden;
}
.icon-header-img{
	width:.6rem;
	border-radius: 50%;
}
.yd-list-theme4 .yd-list-item{
	height: 1.2rem;
}
.yd-list-other{
	-ms-flex-pack:start;
	    justify-content:flex-start;
}
.right{
	padding-left:.1rem;
}
.right span{
}
.p{
	width:28%;
}
.unread:after{
	display: inline-block;
    content: '';
    background: #f46094;
    border-radius: 50%;
    width:.2rem;
    height:.2rem;
    margin-left:.2rem;
}
.ellipsis{
		overflow:hidden;
		white-space:nowrap;
		text-overflow:ellipsis;
}



